body {
  padding: 0;
  margin: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: "Do Hyeon", sans-serif;
}

.container {
  padding: 0;
  margin: 0;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  font-family: "Do Hyeon", sans-serif;
}

.nav_bar {
  width: 100%;
  height: 50px;
  text-align: right;
}

.gmail {
  color: black;
  text-shadow: 1px 1px white, -1px -1px white, 1px -1px white, -1px 1px white;
  line-height: 4;
}

.gmail:hover {
  text-decoration: underline;
}

.profile_img {
  float: right;
  margin: 10px 20px;
  width: 35px;
  height: 35px;
  background-color: rgba(255, 255, 255, 0.5);
  border: 3px solid lightgray;
  border-radius: 100%;
  cursor: pointer;
}

.countdown {
  position: absolute;
  top: 7vh;
  width: 100%;
}

.times {
  width: 100%;
  text-align: center;
  margin-top: 120px;
  font-family: "Do Hyeon", sans-serif;
}

.time_div_date {
  display: inline-block;
  width: 330px;
  text-align: right;
  margin-right: 10px;
}

.time_group {
  display: inline-block;
  padding: 0;
  margin: 0;
}

.time_div {
  display: inline-block;
  width: 280px;
  text-align: right;
  margin-right: 20px;
}

.time {
  font-size: 170px;
  text-shadow: 1px 1px white, -1px -1px white, -1px 1px white, 1px -1px white;
  margin: 25px;
}

.time_des {
  font-size: 35px;
  text-shadow: 1px 1px white, -1px -1px white, -1px 1px white, 1px -1px white;
}

.bar {
  position: absolute;
  top: 70vh;
  left: 30vw;
  margin: 0 auto;
  /* width: 575px; */
  width: 40vw;
  border-radius: 30px;
  border: 1px solid #dcdcdc;
  background-color: white;
}
.bar:hover {
  box-shadow: 1px 1px 8px 1px #dcdcdc;
}
.bar:focus-within {
  box-shadow: 1px 1px 8px 1px #dcdcdc;
  outline: none;
}

.google_img {
  height: 20px;
  position: relative;
  top: 5px;
  left: -10px;
}

.searchbar {
  height: 45px;
  border: none;
  /* width: 500px; */
  width: 35vw;
  font-size: 16px;
  outline: none;
}

.chat_btn {
  position: absolute;
  bottom: 5vh;
  right: 5vw;
  background-color: white;
  border-radius: 30%;
  padding: 10px;
  cursor: pointer;
}

.chat_div {
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 350px;
  height: 550px;
  background-color: white;
  border-radius: 3%;
  box-shadow: 1px 1px 8px 1px gray;
  overflow: hidden;
  opacity: 0%;
  font-family: "Nanum Gothic", sans-serif;
}

.chat_nav {
  width: 100%;
  height: 30px;
  background-color: #6cc0ff;
}

.close_chat {
  margin-right: 10px;
  float: right;
  cursor: pointer;
  font-size: 25px;
  font-weight: normal;
  color: black;
}

.chat_title {
  font-size: 20px;
}

.chat_container {
  width: 100%;
  height: 480px;
  background-color: #90e4ff;
  overflow: auto;
}

.chat_input_div {
  width: 100%;
  height: 40px;
  background-color: rgba(240, 240, 240, 1);
  text-align: left;
}

.file_input {
  display: none;
}

.file_label {
  padding: 20px 15px;
  background-color: none;
  color: black;
  cursor: pointer;
}

.chat_input {
  width: 70%;
  height: 40px;
  border: none;
  padding-left: 10px;
  display: inline-block;
}

.chat_input:focus {
  outline: none;
}

.chat_send_btn {
  width: 15%;
  height: 40px;
  border: none;
  cursor: pointer;
}

.chat_box {
  display: inline-block;
  width: 90%;
  background-color: white;
  border-radius: 35px;
  padding: 0 10px;
  margin: 10px 0;
  text-align: left;
}
.chat_profile_img {
  width: 30px;
  height: 30px;
  border-radius: 100%;
  margin: 10px;
  float: left;
}
.chat_profile_name {
  display: inline-block;
  vertical-align: top;
  font-size: 15px;
  margin-top: 15px;
  padding: 0;
}
.chat_text {
  display: inline-block;
  vertical-align: top;
  font-size: 15px;
  margin: 15px;
  margin-bottom: 0;
  padding: 0;
}
.chat_img {
  width: 100px;
  height: 100px;
}

.chat_time {
  display: inline-block;
